  display: block;
  overflow: hidden;
}

img {
  width: 100%;
  height: auto;
  object-fit: cover;
  transition: var(--transition);
  display: block;
}

img:hover {
  transform: scale(1.4);
}

ul, h1, h2, h3, p {
  margin: 0;
  padding: 0;
  list-style: none;
}

section {
  padding: 24px 0;
}

.icon {
  width: 32px;
  height: 32px;
  fill: currentColor;
  transition: var(--transition);
  display: flex;
}

.info {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.info-item {
  color: #000;
  cursor: pointer;
  flex-direction: column;
  flex-basis: calc((100% - var(--indent) * (var(--items)  - 1) ) / var(--items) );
  align-items: center;
  gap: 4px;
  display: flex;
}

.info-text {
  transition: var(--transition);
}

:root {
  --transition: 250ms cubic-bezier(.65, .08, .68, .96);
  --ligth-color: #d1e8e2;
  --hov-color: #67d3bd;
  --shad-color: #8fd2c5;
  --border-color: #158772;
  --border-shadow: #b5f0e5;
  --indent: 4px;
  --items: 4;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  opacity: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.search-btn {
  cursor: pointer;
  color: var(--title-primary);
  background-color: var(--ligth-color);
  border: 0;
  border: 2px solid var(--border-color);
  transform-style: preserve-3d;
  transition: color var(--transition), transform var(--transition), background-color var(--transition);
  border-radius: .75em;
  outline: none;
  padding: 8px;
  position: relative;
}

.search-btn:before {
  content: "";
  width: 100%;
  height: 80%;
  background-color: var(--shad-color);
  border-radius: inherit;
  transition: transform var(--transition), box-shadow var(--transition);
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  transform: translate3d(0, .75em, -1em);
}

.search-btn:hover, .search-btn:focus-visible {
  color: var(--menu-color-accent);
  background-color: var(--hov-color);
  transform: translate(0, .25em);
}

.search-btn:hover:before, .search-btn:focus-visible:before {
  transform: translate3d(0, .5em, -1em);
}

.container {
  width: 300px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 8px;
