@keyframes bounce-up {
  from {
    transform: translateY(-5px);
  }

  to {
    transform: translateY(5px);
  }
}

.button-up {
  cursor: pointer;
  width: 40px;
  height: 40px;
  opacity: 1;
  z-index: 10;
  color: var(--white-color);
  opacity: .7;
  background-color: var(--border-color);
  transition: color var(--transition), box-shadow var(--transition), opacity var(--transition);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  animation: bounce-up 1.2s linear infinite alternate both;
  display: flex;
  position: fixed;
  bottom: 30px;
  right: 30px;
}

